$red: #ef5350;
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
}
body {
  display: flex;
  height: 100vh;
  max-height: 100vh;
}
.main-container {
  width: 100vw;
  box-sizing: border-box;
  overflow: none;
  display: flex;
  flex-direction: column;
}

.header {
  padding: 1rem;
  box-shadow: 0px 0px 10px gray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 12vh;
  z-index: 4;

  .header-menu {
    display: flex;
  }
}

.input-container {
  display: flex;
  margin-left: 2rem;
  input {
    font-size: 1.2rem;
    border: none;
    outline: none;
    border: 2px solid $red;
    border-right: none;
    border-radius: 0.4rem 0 0 0.4rem;
    padding-left: 0.5rem;
  }

  .search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    background-color: #212121;
    color: white;
    border-radius: 0 0.4rem 0.4rem 0;
    font-weight: bold;
    font-size: 1.3rem;
    cursor: pointer;
  }
}

.date-country {
  display: flex;
  align-items: center;
}

.sub-container {
  display: flex;
  height: 88vh;
}

.sidebar {
  width: 15rem;
  height: 100%;
  box-shadow: 0 5px 10px gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .close-sidebar-button {
    display: none;
  }
}

.open-sidebar-button {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: bold;
  font-size: 2rem;
  background-color: #ef5350;
  color: white;
  height: 12vh;
  width: 3rem;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 25;

  @media screen and (min-width: 700px) {
    display: none;
  }
}

.categories-heading {
  margin: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-button {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  border: none;
  border-top: 2px solid gray;
  outline: none;
  font-weight: bold;
  background-color: white;
  cursor: pointer;
  color: gray;

  @media screen and (max-width: 700px) {
    margin-bottom: 5rem;
    border-bottom: 2px solid gray;
  }
}

.about-button:hover {
  border-top: 2px solid black;
  color: black;
}

.about-button:active {
  border-top: 2px solid $red;
  color: $red;
}

.modal-container {
  background-color: rgba(128, 128, 128, 0.6);
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 10;
  font-size: 1.6rem;

  .modal-window {
    height: 30rem;
    max-width: 40rem;
    background-color: white;
    border-radius: 1rem;
    border: 2px solid $red;
    padding: 1em;

    @media screen and (max-width: 700px) {
      max-width: 90%;
    }

    @media screen and (max-width: 450px) {
      max-width: 90%;
      font-size: 1.4rem;
    }

    @media screen and (max-width: 350px) {
      max-width: 90%;
      font-size: 1.2rem;
    }

    .modal-heading {
      font-size: 2rem;
      font-weight: bold;
      display: flex;
      justify-content: space-between;

      i {
        cursor: pointer;
      }
    }

    .modal-information {
      margin-top: 5rem;
      height: 100%;

      a {
        color: $red;
      }
    }
  }
}

.open-modal {
  display: flex;
}

.home {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-height: 100%;
  .ticker {
    background-color: #ffca28;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    div {
      width: 10rem;
      font-weight: bold;
      padding: 0.5rem;
      color: white;
      background-color: $red;
    }

    ul {
      display: flex;
      li {
        margin: 0 1rem;
      }
    }

    @media screen and (max-width: 900px) {
      display: none;
    }
  }
}

.scroll-to-top {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  right: 0;
  bottom: 0;
  margin: 1rem;
  margin-right: 1.5rem;
  background-color: $red;
  height: 3.5rem;
  width: 3.5rem;
  border-radius: 50%;
  color: white;

  @media screen and(min-width: 1000px) {
    cursor: pointer;
    &:hover {
      animation: shakeY 1s infinite;
    }
  }

  @media screen and (max-width: 700px) {
    margin-bottom: 4rem;
  }

  &:active {
    background-color: #ffca28;
    color: black;
  }
}

.card-grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-auto-rows: 20rem;
  gap: 1rem;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 2rem;
  &::-webkit-scrollbar {
    width: 1rem;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(128, 128, 128);
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: $red;
  }

  &::-webkit-scrollbar-track {
    background-color: rgba(128, 128, 128, 0.703);
  }

  @media screen and (max-width: 400px) {
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  }
}

.loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;

  .loader-img {
    height: 200px;
    width: 200px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('data:image/png;base64,');
    animation: spin-loader 1s infinite linear;
  }

  @keyframes spin-loader {
    100% {
      transform: rotate(-360deg);
    }
  }

  @keyframes shakeY {
    50% {
      transform: translateY(-5px);
    }
    100% {
      transform: none;
    }
  }
}

@media screen and (max-width: 700px) {
  .sidebar {
    display: none;
    background-color: #ffffff;
    width: 100%;
    height: 88vh;
    position: absolute;
    z-index: 3;
    padding: none;
    margin: 0;

    .close-sidebar-button {
      padding: 1rem;
      display: block;
      display: flex;
      justify-content: right;
      width: 100%;
      font-size: 2rem;
    }
  }

  .sidebar-active {
    display: flex;
  }
}

@media screen and (max-width: 900px) {
  .home .ticker {
    display: none;
  }
}
